const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")

const app = getApp()
export default {
	name: '',
	data() {
		return {
			status: true,
			orderId: 0
		}
	},
	onLoad() {
		let pages = getCurrentPages();
		let { options } = pages.pop();
		if(options.status == 0) {
			this.status = false;
		}else {
			this.status = true;
		}
		wx.hideShareMenu()
	},
	methods:{
		//重新支付
		payOrder() {
			wx.navigateTo({
				url: '/pages/user/order'
			})
		}
	}
}
